@use "sass:map";
@use "sass:list";

$map: (
  min-width: 200px,
  max-width: 1000px,
);

header {
  .main-width {
    a {
      background-color: purple;
    }
    .menu {
      :first-child {
        min-width: map-get($map, min-width);
        max-width: map-get($map, max-width);
        background-color: red;
      }
      :last-child {
        min-width: map-get($map, min-width);
        max-width: map-get($map, max-width);
        background-color: aqua;
      }
    }
  }
}
